<template>
  <div class="login">
    <div class="login-center--wrapper">
      <p class="title"></p>
      <p style="margin-top: 30px;">
        <el-input
          placeholder=""
          prefix-icon="el-icon-user-solid"
          v-model="username"
          clearable
        ></el-input>
      </p>
      <p style="margin-top: 20px;">
        <el-input
          placeholder=""
          v-model="password"
          show-password
          clearable
          prefix-icon="el-icon-lock"
          @keyup.enter.native="signIn()"
        ></el-input>
      </p>
      <p style="margin-top: 30px;">
        <el-button type="primary" style="width: 100%;" @click="signIn()">
          <span v-show="!signingIn"> </span>
          <i v-show="signingIn" class="el-icon-loading"></i>
        </el-button>
      </p>
      <p style="margin-top:10px;text-align: right;">
        <el-link type="primary" @click="registDialogVisible=true"></el-link>
      </p>
    </div>
    <el-dialog :close-on-click-modal="false" :visible.sync="registDialogVisible" width="600px">
      <template slot="title">
        <div class="regist-dialog-title">
          <img alt="logo" src="@/assets/image/logo.png" width="32px" />
          <h3></h3>
        </div>
      </template>

      <el-form
        style="margin:0 35px"
        :model="registForm"
        status-icon
        :rules="rules"
        ref="registForm"
        label-width="100px"
        label-position="left"
      >
        <el-form-item prop="username" label="">
          <el-input
            v-model="registForm.username"
            placeholder=""
            prefix-icon="el-icon-user-solid"
          ></el-input>
        </el-form-item>
        <el-form-item prop="email" label="">
          <el-input
            v-model="registForm.email"
            placeholder=""
            prefix-icon="el-icon-message"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            type="password"
            v-model="registForm.password"
            autocomplete="off"
            placeholder="6"
            prefix-icon="el-icon-lock"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="checkPassword">
          <el-input
            type="password"
            v-model="registForm.checkPassword"
            autocomplete="off"
            placeholder=""
            prefix-icon="el-icon-lock"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="registDialogVisible = false;$refs['registForm'].resetFields();"> </el-button>
        <el-button @click="resetForm('registForm')"> </el-button>
        <el-button type="primary" @click="submitForm('registForm')"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  data() {
    // 
    var validateName = (rule, value, callback) => {
      if (value.trim() === "") {
        callback(new Error(""));
      } else {
        // 
        const user = {};
        user.username = value;
        this.$http
          .post("/user/checkName", user)
          .then(res => {
            if (res.existsName) {
              callback(new Error("!"));
            } else {
              callback();
            }
          })
          .catch(() => {
            callback(new Error("..."));
          });
      }
    };

    // 
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.registForm.checkPassword !== "") {
          this.$refs.registForm.validateField("checkPassword");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.registForm.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };

    return {
      // 
      signingIn: false,
      username: "",
      password: "",
      // 
      registDialogVisible: false,
      registForm: {
        username: "",
        email: "",
        password: "",
        checkPassword: ""
      },
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { validator: validateName, trigger: "blur" }
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "email",
            message: "",
            trigger: ["blur", "change"]
          }
        ],
        password: [
          { required: true, validator: validatePass, trigger: "blur" },
          { min: 6, message: "6", trigger: "blur" }
        ],
        checkPassword: [
          { required: true, validator: validatePass2, trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    ...mapMutations(["setToken", "setUser", "setUid"]),

    /**
     * 
     */
    signIn() {
      if (!this.username) {
        this.$message({
          message: "",
          type: "warning"
        });
        return false;
      }
      if (!this.password) {
        this.$message({
          message: "",
          type: "warning"
        });
        return false;
      }
      this.signingIn = true;
      const user = {};
      user.username = this.username;
      user.password = this.password;
      this.$http
        .post("/user/login", user)
        .then(res => {
          let user = res.user;
          let uid = user.id;
          let token = res.token;

          if (token) {
            this.signingIn = false;
            this.$message({
              message: "",
              type: "success",
              duration: 1000
            });
            // Cookies
            this.setToken(token);
            this.setUser(user);
            this.setUid(uid);
            // 
            let path = user.role === "admin" ? "/admin/home" : this.$route.query.redirect || "/home/mine";
            // 
            this.$router.push(path);
          } else {
            this.signingIn = false;
            this.$message({
              message: res.message,
              type: "warning",
              duration: 1000
            });
          }
        })
        .catch((err) => {
          this.signingIn = false;
          this.$message({
            message: err.message,
            type: "error",
            duration: 1500
          });
        });
    },

    /**
     * 
     */
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$http
            .post("/user/regist", this.registForm)
            .then(() => {
              this.registDialogVisible = false;
              this.username = this.registForm.username; // 
              this.$message({
                message: "!",
                type: "success",
                duration: 1000
              });
            })
            .catch(() => {
              this.$alert("!");
            });
        } else {
          return false;
        }
      });
    },

    /**
     * 
     */
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style scoped>
.login {
  width: 100%;
  height: 100%;
}
.login-center--wrapper {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
}
.title {
  font-size: 26px;
  color: #4caaf5;
  text-align: center;
  margin-top: 20px;
}
.regist-dialog-title {
  display: flex;
  align-items: center;
}
.regist-dialog-title h3 {
  margin: 0 0 0 5px;
  display: inline;
  font-weight: 500;
}
</style>
