import axios from "axios";
import Cookies from "js-cookie";
import router from "@/router";
import { Message } from "element-ui";

// 
axios.defaults.timeout = 20000;
// URL
axios.defaults.baseURL = "http://localhost:39000";
const domain = axios.defaults.domain = "http://localhost:8080"

//
axios.interceptors.request.use(
  config => {
    if (Cookies.get("SURVEY_TOKEN")) {
      config.headers["Authorization"] = Cookies.get("SURVEY_TOKEN");
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

//
axios.interceptors.response.use(
  response => {
    //HTTP200
    //
    if (response.status === 200) {
      if (response.data) {
        // 200
        if (response.data.code === "200") {
          return Promise.resolve(response);
        } else {
          return Promise.reject(response);
        }
      } else {
        return Promise.resolve(response);
      }
    } else {
      return Promise.reject(response);
    }
  },
  error => {
    const err = error.response.data;
    if (err.message === "401") {
      Message({
        message: "",
        type: "warning"
      });
      Cookies.remove("SURVEY_TOKEN");
      Cookies.remove("SURVEY_USER");
      Cookies.remove("SURVEY_UID");
      router.push("/login");
    }
    return Promise.reject(error);
  }
);

/**
 * get
 */
const get = (url, params) => {
  return new Promise((resolve, reject) => {
    axios
      .get(url, {
        params: params
      })
      .then(res => {
        //
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data);
      });
  });
};

/**
 * post
 */
const post = (url, params) => {
  return new Promise((resolve, reject) => {
    axios
      .post(url, params)
      .then(res => {
        //
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data);
      });
  });
};

/**
 * 
 */
const saveSurvey = (url, params) => {
  return new Promise((resolve, reject) => {
    const savingMsg = Message({
      message: '<strong style="color:#333">...</strong>',
      dangerouslyUseHTMLString: true,
      iconClass: " ",
      center: true,
      duration: 0,
      offset: 68,
      customClass: "center-msgbox"
    });
    axios
      .post(url, params)
      .then(res => {
        savingMsg.close();
        Message({
          message: '<strong style="color:#333"></strong>',
          dangerouslyUseHTMLString: true,
          iconClass: " ",
          center: true,
          duration: 1000,
          offset: 68,
          customClass: "center-msgbox"
        });
        resolve(res.data);
      })
      .catch(err => {
        savingMsg.close();
        Message({
          message: '<strong style="color:#333"></strong>',
          dangerouslyUseHTMLString: true,
          iconClass: " ",
          center: true,
          duration: 1000,
          offset: 68,
          customClass: "center-msgbox"
        });
        reject(err.data);
      });
  });
};

export default {
  get,
  post,
  saveSurvey,
  domain
};
